app.createGameTemplate = function (numPairs) {

createGameTemplate(9);

function createGameTemplate (numPairs) {

    var template = _.template($('#gameBoard').html(), { variable: 'm' });
    var html = template({
    cards: app.gameDeck(numPairs)
    });

    $('.main-content').html(html);
    $('.game-icon').hide();
    app.setTimer();
  }

};
